.education {
    height: calc(93vh - var(--navbar-height));
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-direction: column;
    background-color: var(--color-black);
    width: 100vw;
    padding: 3vw;
}

.education-overHead {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: row;
}

.education-row {
    width: 100vw;
    padding-bottom: 4vw;
    display: flex;
    justify-content: space-around;
    text-align: center;
    align-items: center;
    flex-direction: row;
}

.school {
    cursor: pointer;
    border-width: 2px;
}

.school-image {
    height: 24vh;
}

.education-header {
    color: var(--color-white);
    margin-left: 20px;
}

.white {
    color: var(--color-white);
}


@media only screen and (max-width: 450px) {
    .education {
        height: calc(100vh - var(--navbar-height));
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        flex-direction: column;
        background-color: var(--color-black);
        width: 100vw;
        padding: 3vw;
    }

    .education-row {
        flex-direction: column;
        padding-bottom: 4vw;
    }

    .school-image {
        height: 20vh;
    }
}