.work {
    height: calc(100vh - var(--navbar-height));
    display: flex;
    justify-content: space-around;
    align-items: flex-start;
    flex-direction: column;
    background-color: var(--color-black);
    width: 100vw;
    padding: 3vw;
}

.work-overHead {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: row;
}

.work-row {
    flex: 1;
    width: 100vw;
    display: flex;
    justify-content: space-around;
    text-align: center;
    align-items: center;
    flex-direction: row;
}

.work-logo {
    height: 55px;
}

.job {
    cursor: pointer;
}

.job-image {
    width: 34vw;
}

.job-image-amazon {
    width: 22vw;
}

.work-header {
    color: var(--color-white);
    margin-left: 20px;
}

.white {
    color: var(--color-white);
}

@media only screen and (max-width: 650px) {
    .work-row {
        flex-direction: column;
    }

    .job-image {
        width: 250px;
    }

    .work-logo {
        height: 40px;
    }

    .job-image-amazon {
        width: 170px;
    }
}